.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
    
}

.about__me{
    
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary),transparent);
    display: grid;
    place-items: center;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about__me-image:hover{
    transform: rotate(0deg);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 0.5rem;
}

.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.95rem;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin: 2rem 0 2rem;
    color: var(--color-light);
}


/* ================ Media Queries (Medium Devices) ==================*/

@media screen and (max-width: 1024px) {
        .about__container{
            grid-template-columns: 1fr;
            gap: 0;
            
        }

        .about__me{
            width: 50%;
            margin: 2rem auto 4rem;
        }

        .about__content p{
            margin: 1rem 0 1rem;
            font-size: 12px;
        }
    }

    /* ================ Media Queries (Small Devices) ==================*/

@media screen and (max-width: 600px) {
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
        width: 100%;
    }
    .about__me{
        width: 250px;
        margin: 0 auto 3rem;
    } 

    .about__card{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content{
        
        text-align: center;
        font-size: 5px;
        padding: 20px;
    }

    .about__content p{
        margin: 1rem 0;
        
    }
    a{
        font-size: 12px;
    }
    .about__card{
        background: var(--color-bg-variant);
        border: 1px solid transparent;
        border-radius: 1rem;
        padding: 0.5rem;
        text-align: center;
        transition: var(--transition);
    }
}